
.progress {
  width: 100%;
  height: 1.5rem;
  background-color: grey;
  border-radius: 1rem;
  overflow: hidden;
}
progress {
  width: 100%;
  height: 100%;
  appearance: none;
  border: none;
  background-color: #2196F3;
}
progress::-webkit-progress-bar {
  background-color: grey;
}
progress::-webkit-progress-value {
  background-color: #2196F3;
}
